@import 'settings/fonts';

html,
body {
  background-color: #FFFFFF;
  color: #636B6F;
  font-family: 'Montserrat', sans-serif;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  text-align: center;
}

.container {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

header {
  text-align: center;
  flex: 0 1 20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.title {
  font-size: 84px;
  font-weight: 100;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 1 auto;
}

.input-container {
  padding: 10px;
}

table {
  border-collapse: collapse;
}

table,
th,
td {
  border: 1px solid #DADADA;
  padding: 5px;
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 0 0 5vh;
}
